/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Fetch, Doggie, Fetch!","date":"08 May 2007","slug":"/2007-05-08-fetch-doggie-fetch/","tags":"Y2007"},"html":"<p><a href=\"http://sprechblase.wordpress.com/2007/05/06/aua/\">Cem</a> passed on this questionaire to me. For some reason, in German these questionaires are called a <em>Stöckchen</em>, a stick you throw for a dog to fetch. So I'm obediently picking up the stick...</p>\n<p><strong>Do you use social bookmarking/networking sites like Digg, Yigg, Mister Wong or Del.Icio.Us?</strong> I use del.icio.us, but not regularly. It's much easier to share links on Google Reader.</p>\n<p><strong>Do you watch videos at portals like YouTube, Sevenload or DailyMotion an?</strong> Not really, I find the written word more interesting than moving pictures.</p>\n<p><strong>Do you know of or use Flickr?</strong> I use Flickr only as an image store. I don't use any of the social features.</p>\n<p><strong>What do you think of services like Twitter or Frazr?</strong> I don't know yet. I've tried Twitter, but I don't get it.</p>\n<p><strong>How many RSS feeds do you subscribe to?</strong> 322 at the moment.</p>\n<p><strong>Do you read them all daily?</strong> No. I have my feeds grouped by priority, some to read immediately, some to read daily, and some to check when I have the chance.</p>\n<p><strong>How many blogs do you write for (own or as co-author)?</strong> Only one. I've tried starting a second blog for non-personal topics, but found it too schizophrenic.</p>\n<p><strong>Do you comment in other blogs?</strong> Not as often as I would like.</p>\n<p><strong>Who is your personal Blog King of Germany?</strong> I didn't know we had a king. I thought we were an autonomous collective. (<a href=\"http://www.imdb.com/title/tt0071853/quotes\">source</a>)</p>\n<p><strong>Do you think weblogs belong to the futures, or are they just a temporary hype?</strong> I've been \"blogging\" in one form or another since I was 16 years old. Back then I used a typewriter and a mimeograph, now I use the internet. It's just a medium.</p>\n<p><strong>Will you blog be a part of that future?</strong> I will be writing in some form or another.</p>\n<p><strong>Are we alone in the Universe?</strong> Does it matter? Knowing for sure one way or the other wouldn't change me or my life at all.</p>\n<p><strong>Who's next to answer these questions?</strong> Whoever is brave enough to try.</p>"},"next":{"fields":{"title":"","date":"07 May 2007","slug":"/2007-05-07-background-on-mccafe/"}},"previous":{"fields":{"title":"Flash Slideshow with Captions","date":"08 May 2007","slug":"/2007-05-08-flash-slideshow-with-captions/"}}},{"node":{"fields":{"title":"","date":"07 May 2007","slug":"/2007-05-07-background-on-mccafe/","tags":"Y2007"},"html":"<p>MamaMaus was sent a trade magazine article on <a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/05/mccafe.pdf\">McCafé in Germany</a> (PDF, 770K, in German) that she wanted to share. It explains the concept (coffee shop atmosphere with the convenience and value of McDonald's) and some of the details of operation.</p>\n<p>Our soon-to-be restaurant does not have a McCafé yet, but we want one as quickly as we can and our it's included in our financial planning for this year. Maybe it will look something like this (design concept Generation Wenge)...</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/05/mccafe-design-wenge.jpg\" alt=\"McCafé Design Wenge\" /></p>"},"next":{"fields":{"title":"Corrupted UTF-8 characters with PHP and MySQL","date":"05 May 2007","slug":"/2007-05-05-corrupted-utf-8-characters-with-php-and-mysql/"}},"previous":{"fields":{"title":"Fetch, Doggie, Fetch!","date":"08 May 2007","slug":"/2007-05-08-fetch-doggie-fetch/"}}},{"node":{"fields":{"title":"Corrupted UTF-8 characters with PHP and MySQL","date":"05 May 2007","slug":"/2007-05-05-corrupted-utf-8-characters-with-php-and-mysql/","tags":"Y2007"},"html":"<p>Someone recently asked me about corrupted characters in a PHP-MySQL project. They had recent stable versions of everything, MySQL 5.0 and PHP 5.1, using UTF-8 for everything, and they were getting corrupted accented characters saved in the database, junk with 'Ã's was coming out. They were asking me if there were perhaps some secret PHP setting they were missing to make things right. Not that I knew of.</p>\n<p>I would have thought that in this day and age using UTF-8 with PHP and MySQL would just work. To try this out for myself, I downloaded some simple tutorial code, set up databases and HTML with UTF-8, and started creating records with names like Scött Hänsön and Mäcky Möüße. And sure enough, I got junk.</p>\n<p>PHP and MySQL don't just work with UTF-8. In particular, the client connection from PHP to MySQL defaults to latin1. There don't seem to be any PHP settings to change this. On the MySQL side there are 'default-character-set' settings you can put in various sections of the my.cnf, but for me they didn't seem to help.</p>\n<p>I found two ways to solve the problem. The first is to issue a <code>mysql_query(\"SET NAMES 'utf8')</code> immediately after every <code>mysql_connect</code> to manually switch the connection to utf8. That's a huge pain if you have a lot of code (even in my mini 5-file test-case I forgot one instance at first).</p>\n<p>The other is to add a <code>init-connect='SET NAMES utf8'</code> setting to the <code>[mysqld]</code> section of my.cnf. <code>init-connect</code> automatically executes SQL commands at the beginning of every client connection. The catch is the commands won't be executed for a user with SUPER privileges, so if you're lazy and using 'root' to connect to your database, it won't work.</p>\n<p>By using various combinations of <code>&lt;meta charset=\"blah\"/&gt;</code> in the HTML and <code>SET NAMES</code> for the database connection, you can create interesting errors. The 'Ã' errors seem to occur when correctly sent utf-8 is incorrectly saved over a latin1 connection, then read back out as utf-8. My friend said he was using <code>SET NAMES 'utf8'</code> with all his connects, I'm guessing he forgot it for INSERTs and UPDATEs. That's just a hunch.</p>\n<p>I wasn't able to find a clear explanation of this in Google (the closest was in the comments to <a href=\"http://www.php.net/manual/en/function.mysql-client-encoding.php\">mysql&#95;client&#95;encoding()</a> in the PHP documentation), so I thought write this up here. It's clear to me, at least.</p>"},"next":{"fields":{"title":"Top 7 Things System Administrators Forget to Do","date":"03 May 2007","slug":"/2007-05-03-top-7-things-system-administrators-forget-to-do/"}},"previous":{"fields":{"title":"","date":"07 May 2007","slug":"/2007-05-07-background-on-mccafe/"}}}],"pathPrefix":"","first":false,"last":false,"index":132,"pageCount":1101}}

/***/ })

